@charset "UTF-8";
* {
  font-family: "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
}

body {
  line-height: 1.8;
  font-size: 16px;
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  width: 100%;
  height: auto;
  vertical-align: bottom;
}

html {
  color: #000;
  background: #fff;
}

body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
legend,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ol,
ul {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

q:before,
q:after {
  content: "";
}

abbr,
acronym {
  border: 0;
  font-variant: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select,
button {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  line-height: inherit;
  *font-size: 100%;
}

legend {
  color: #000;
}

* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

*::before,
*::after {
  box-sizing: border-box;
}

/* iOSでのデフォルトスタイルをリセット */
[type=submit],
[type=button] {
  border-radius: 0;
  -webkit-box-sizing: content-box;
  -webkit-appearance: button;
  appearance: button;
  border: none;
  box-sizing: border-box;
  cursor: pointer;
}

[type=submit]::-webkit-search-decoration,
[type=button]::-webkit-search-decoration {
  display: none;
}

[type=submit]::focus,
[type=button]::focus {
  outline-offset: -2px;
}

.u-link-btn {
  display: block;
  width: 100%;
  height: 100%;
}

@media screen and (min-width: 500px) {
  .u-break-sm {
    display: none;
  }
}
@media screen and (min-width: 768px) {
  .u-break-md {
    display: none;
  }
}
.u-break-lg {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-break-lg {
    display: inline;
  }
}

.l-container-xl, .l-container-l, .l-container, .l-container-s {
  width: 92%;
  margin: 0 auto;
}

.l-container-s {
  max-width: 800px;
}

.l-container {
  max-width: 1080px;
}

.l-container-l {
  max-width: 1200px;
}

.l-container-xl {
  max-width: 1400px;
}

.footer {
  padding: 24px 0;
  text-align: center;
  margin-top: 80px;
}

.header {
  display: flex;
  justify-content: space-between;
  padding: 8px 40px;
  opacity: 0.5;
  align-items: center;
  font-weight: bold;
  background-color: #fff;
  width: 100%;
  position: fixed;
  z-index: 100;
}
.header-menu {
  display: flex;
  justify-content: space-between;
  font-size: 40px;
}
.header-menu__item {
  margin-left: 80px;
  font-family: "Ephesis", cursive;
  transition: 0.2s;
  border-radius: 16px;
  padding: 0 8px;
}
.header-menu__item:hover {
  display: block;
  box-shadow: 5px 5px 10px gray;
}
.header-menu__item:focus {
  transform: translate(5px, 5px);
  box-shadow: none;
}

.btn {
  padding: 8px 24px;
  border: 3px solid #000;
  color: #fff;
  background-color: #000;
  border-radius: 32px;
  transition: 0.2s;
  box-shadow: 5px 5px 5px gray;
}
.btn:hover {
  cursor: pointer;
}
.btn:focus {
  transform: translate(5px, 5px);
  box-shadow: none;
}

.sec-ttl {
  font-weight: bold;
  font-size: 96px;
  font-family: "Ephesis", cursive;
  color: red;
  margin: 80px;
  position: relative;
  display: inline-block;
}
.sec-ttl::after {
  content: "";
  position: absolute;
  display: block;
  height: 3px;
  width: 0;
  bottom: 30%;
  left: 50%;
  transform: translateX(-50%);
  background: linear-gradient(to bottom, #fcfc93, red);
  transition: 2s ease-in-out;
}
.sec-ttl.is-animated::after {
  width: 100%;
}

.mv-ttl {
  font-weight: bold;
  font-size: 96px;
}

.news-ttl {
  font-weight: bold;
  font-size: 32px;
}

.header-ttl {
  font-size: 56px;
  font-family: "Ephesis", cursive;
  transition: 0.2s;
}

.about-sec {
  text-align: center;
  background: url("../images/about.jpg");
  background-size: cover;
}

.about-inner {
  display: flex;
  align-items: flex-start;
  margin-top: 40px;
  padding-bottom: 120px;
}

.myProfile {
  text-align: center;
  width: 60%;
  color: #fff;
}
.myProfile img {
  width: 30%;
  height: 280px;
  object-fit: cover;
  border-radius: 50%;
  box-shadow: 0px 0px 20px white;
}
.myProfile__name {
  font-weight: bold;
  font-size: 40px;
  margin-top: 56px;
}

.cards {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: 80px 80px 0;
  height: 600px;
}

.card-inner {
  width: 250px;
  height: 150px;
  border-radius: 20px;
  position: relative;
  transition: 1.5s ease-in-out;
  transform-style: preserve-3d;
}
.card-inner:hover {
  transform: rotateY(180deg);
}

.side {
  position: absolute;
  text-align: center;
  width: 100%;
  height: 100%;
  padding: 20px 0;
  color: #fff;
  transform-style: preserve-3d;
  backface-visibility: hidden;
  border-radius: 24px;
  font-weight: bold;
  font-size: 24px;
}

.card-content {
  transform: translatez(70px) scale(0.8);
}

.front {
  z-index: 2;
  background-size: cover;
  background-image: url("../images/card-front.jpg");
}

.back {
  transform: rotateY(180deg);
  z-index: 0;
  background-size: cover;
  background-image: url("../images/card-back.jpg");
}

.myNews {
  background-color: rgba(255, 255, 255, 0.3);
  width: 40%;
  padding: 40px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 40px;
}
.myNews ul {
  width: 40%;
}

.news {
  background-color: rgba(255, 255, 255, 0.5);
  margin-top: 16px;
  text-align: left;
  align-items: center;
  padding: 0 8px;
  border-radius: 8px;
  position: relative;
}
.news__date {
  font-weight: bold;
}
.news:first-child::before {
  content: "";
  position: absolute;
  top: 8px;
  right: 100px;
  border-top: 4px solid transparent;
  border-right: 20px solid red;
  border-left: 20px solid transparent;
  border-bottom: 4px solid transparent;
}
.news:first-child::after {
  content: "New!!";
  position: absolute;
  top: -10px;
  right: 30px;
  font-weight: bold;
  background-color: red;
  color: #fff;
  border-radius: 32px;
  padding: 4px 16px;
}

.more {
  margin: 40px 0;
  transition: 0.2s;
}

.contact-sec {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}
.contact-sec::after {
  content: "お気軽にお問い合わせください";
  position: absolute;
  top: 220px;
}

.contact-item {
  margin-top: 24px;
}
.contact-item__header {
  display: flex;
  justify-content: space-between;
  font-weight: bold;
}
.contact-item__header.need {
  position: relative;
}
.contact-item__header.need::after {
  content: "必須";
  position: absolute;
  top: 0;
  right: 0;
  color: #fff;
  background-color: red;
  padding: 0 8px;
  border-radius: 16px;
}
.contact-item__header.free {
  position: relative;
}
.contact-item__header.free::after {
  content: "任意";
  position: absolute;
  top: 0;
  right: 0;
  color: #fff;
  background-color: #000;
  padding: 0 8px;
  border-radius: 16px;
}

.submit {
  margin-top: 24px;
}

.mv {
  background: url("../images/mv.jpg");
  background-size: 120%;
  background-position: bottom 10% left;
  width: 100%;
  height: 1200px;
  position: relative;
  overflow: hidden;
}
.mv__copy {
  position: absolute;
  top: 300px;
  left: 40px;
  font-weight: bold;
  font-size: 96px;
  z-index: 10;
  opacity: 0;
  visibility: hidden;
  transform: translateY(200px);
  transition: 1s;
}
.mv__copy.fadeIn {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}
.mv__copy-sub {
  position: absolute;
  top: 360px;
  left: 40px;
  font-weight: bold;
  font-size: 120px;
  line-height: 1.5em;
  font-family: "Ephesis", cursive;
  color: white;
}
.mv__copy-sub span {
  opacity: 0;
}
.mv__copy-sub.js-appear span {
  animation: text_anime_on 1s ease-out forwards;
}

@keyframes text_anime_on {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.scroll-1 {
  position: absolute;
  top: 700px;
  left: 80px;
  font-family: "Ephesis", cursive;
  font-weight: bold;
  transition: 0.3s;
  opacity: 0;
  visibility: hidden;
}
.scroll-1.js-active {
  visibility: visible;
  font-size: 56px;
  color: white;
  opacity: 0.5;
}

