.service-sec {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: url("../images/bg-img.jpg");
  padding-bottom: 120px;
}

.service-req__txt {
  font-size: 24px;
  position: relative;
}
.service-req__txt:not(:first-child) {
  margin-top: 24px;
}
.service-req__txt::before {
  content: "";
  position: absolute;
  height: 0.7em;
  width: 1.5em;
  top: 0.3em;
  left: -2em;
  border-left: 5px solid red;
  border-bottom: 5px solid red;
  transform: rotate(-45deg);
}

.service-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 70%;
  margin-top: 80px;
}
.service-item__img {
  width: 47%;
  height: 350px;
  position: relative;
}
.service-item__img img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  border-radius: 32px;
  box-shadow: 0 0 20px gray;
}
.service-item__img.first:after {
  content: "ヒアリング";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  text-align: center;
  font-size: 48px;
  font-weight: bold;
  color: black;
}
.service-item__img.first:before {
  content: "Consulting";
  position: absolute;
  top: -0.2em;
  left: 0;
  width: 100%;
  text-align: center;
  font-size: 112px;
  font-family: "Ephesis", cursive;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.1);
}
.service-item__img.second:after {
  content: "デザイン";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  text-align: center;
  font-size: 48px;
  font-weight: bold;
  color: black;
}
.service-item__img.second:before {
  content: "Design";
  position: absolute;
  top: -0.2em;
  left: 0;
  width: 100%;
  text-align: center;
  font-size: 112px;
  font-family: "Ephesis", cursive;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.1);
}
.service-item__img.third:after {
  content: "コーディング";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  text-align: center;
  font-size: 48px;
  font-weight: bold;
  color: white;
}
.service-item__img.third:before {
  content: "Coding";
  position: absolute;
  top: -0.2em;
  left: 0;
  width: 100%;
  text-align: center;
  font-size: 112px;
  font-family: "Ephesis", cursive;
  font-weight: bold;
  color: rgba(255, 255, 255, 0.3);
}
.service-item__img.fourth:after {
  content: "アフターフォロー";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  text-align: center;
  font-size: 48px;
  font-weight: bold;
  color: white;
}
.service-item__img.fourth:before {
