  width: 100%;
  text-align: center;
  font-size: 112px;
  font-family: "Ephesis", cursive;
  font-weight: bold;
  color: rgba(255, 255, 255, 0.3);
}
.service-item__txt {
  width: 40%;
  font-size: 18px;
}
.service-item__txt span {
  color: red;
}

.works-sec {
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  margin-bottom: 80px;
}

.skills-inner {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-content: space-between;
  width: 60%;
  height: 1000px;
  margin: 80px 0;
}

.skill {
  width: 30%;
  height: 260px;
}
.skill img {
  object-fit: cover;
}
.skill p {
  font-size: 40px;
  font-weight: bold;
  text-align: center;
  box-shadow: 4px 16px 20px -5px gray;
  margin-top: 4px;
  transition: 0.2s;
}
.skill p:hover {
  transform: translate(5px, 5px);
}

.carousel {
  width: 600px;
  height: 337.5px;
  margin: 0 auto;
  position: relative;
}
.carousel-area {
  height: 100%;
  display: flex;
  position: absolute;
}
.carousel-area__item {
  width: 600px;
  height: 337.5px;
  margin-right: 30px;
}
.carousel__img {
  width: 100%;
  height: 100%;
}

@media screen and (max-width: 600px) {
  .carousel {
    width: 300px;
    height: 168.75px;
  }
  .carousel-area__item {
    width: 300px;
    height: 168.75px;
    margin-right: 0;
  }
}
.arrow-wrap {
  width: 90%;
  height: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.arrow-btn {
  width: 48px;
  height: 48px;
  background-color: rgba(113, 135, 245, 0.8);
  border-radius: 50%;
  transition: 0.2s;
}
.arrow-btn:focus {
  box-shadow: 0px 1px 10px -2px rgba(0, 0, 0, 0.8);
}
.arrow-btn:hover {
  background-color: #334fd8;
  box-shadow: 0px 1px 10px -2px rgba(0, 0, 0, 0.8);
}

.arrow-left {
  position: relative;
}
.arrow-left::before {
  content: "";
  width: 10px;
  height: 10px;
  border-top: 2px solid #fff;
  border-left: 2px solid #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-30%, -80%) rotate(-45deg);
}

.arrow-right {
  position: relative;
}
.arrow-right::before {
  content: "";
  width: 10px;
  height: 10px;
  border-top: 2px solid #fff;
  border-left: 2px solid #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-70%, -80%) rotate(135deg);
}

.pagination {
  width: 150px;
  display: flex;
  justify-content: space-around;
}
.pagination-circle {
  width: 20px;
  height: 20px;
  border: 1px solid #333;
  border-radius: 50%;
  background-color: rgba(83, 97, 223, 0.3);
}
.pagination-circle.target {
  background-color: rgba(83, 97, 223, 0.8);
}